@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  position: relative;
  box-sizing: border-box;
}

p{
  font-size: 18px;
  color: #006890;
}
.Users{
    padding: 60px 0;
}
.textbox{
    width: 100%;
    border: 0;
    outline: none;
}

.rowhover, .rowhover input{
    background-color: #eee;
}


input.textbox-edit{
    width: 100%;   
    background-color: #fff;
}
.error{
    font-size: 12px;
    color: red;
}
label{
    font-size: 14px;
    color: #999;
}
.Stars{
    padding: 60px 0;
}
.ourwork{
    padding:60px 0;
}
.heading{
    font-size: 35px;
    text-align: center;
    color: #005e8c;
    margin-bottom: 25px;
}

.ourwork p{
    text-align: center;
}

.ourwork__button{
    display:flex;
    align-items: center;
    justify-content: center;
}

.ourwork__button button{
    margin: 15px;
}

.whycoose{
    padding: 60px 0;
    background-color: #eee;
}
.Header{
  display: flex;
  background-color: #333;
  padding: 10px;;
}
.Header__item{
  width: 50%;
  display: flex;
  align-items: center;
}
.navbar-brand{
  font-size: 20px;
  color: #999;
  text-transform: uppercase;
  font-weight: 700;
}
